html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Public Sans", sans-serif;
  font-size: 1.8rem;
  font-weight: 400;
  max-width: 144rem;
  width: 100%;
  margin: 0 auto;
}

a {
  text-decoration: none;
  font-weight: 500;
}

button {
  font-weight: 500;
}

p {
  font-size: 1.8rem;
  font-weight: 400;
  line-height: 1.5;
}

h2 {
  font-size: 2.5rem;
  font-weight: 500;
}

h3 {
  font-size: 2.3rem;
  font-weight: 500;
}

h1, h2, h3, h4 {
  margin: 0;
  font-weight: 700;
}

p {
  margin: 0;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.img-fluid {
  width: 100%;
}

/* Firefox */
* {
  scrollbar-width: thin;
  scrollbar-color: hsl(192deg, 70%, 70%) hsl(233deg, 8%, 62%);
}

/* Chrome, Edge, and Safari */
*::-webkit-scrollbar {
  width: 10px;
}

*::-webkit-scrollbar-track {
  background: hsl(233deg, 8%, 62%);
}

*::-webkit-scrollbar-thumb {
  background: linear-gradient(to bottom, hsl(136deg, 65%, 51%) 0%, hsl(192deg, 70%, 51%) 100%);
  border: 1px solid hsl(0deg, 0%, 100%);
}

header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative;
  width: 100%;
  height: 6.4rem;
  padding: 0 2.4rem;
  z-index: 10;
}
header .link-home img {
  margin-bottom: -0.5rem;
}
header .menu {
  position: absolute;
  width: 100%;
  height: 100vh;
  left: 0;
  bottom: 0;
  top: 0;
  z-index: 0;
  padding: 2rem 2.4rem;
  transform: translateX(-100%);
  transition: transform 0.3s ease-in-out;
}
header .menu ul {
  position: relative;
  top: 7rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 2.5rem;
}
header .menu ul li {
  text-align: center;
  padding: 1rem 0;
}
header .menu ul li a {
  font-weight: 400;
}
header .menu.open {
  transform: translateX(0%);
  transition: transform 0.3s ease-in-out;
}
header .access-btn {
  display: none;
}

@media (min-width: 768px) {
  header {
    padding: 0 2.4rem;
    height: 8rem;
    justify-content: space-between;
  }
  header .menu {
    position: relative;
    width: auto;
    height: auto;
    padding: 0;
    transform: translateX(0);
  }
  header .menu ul {
    display: flex;
    flex-direction: row;
    top: 0%;
    padding: 0;
  }
  header .menu ul li {
    text-align: center;
    margin-right: 3rem;
  }
  header .menu ul li:last-of-type {
    margin-right: 0;
  }
  header .menu ul li a {
    position: relative;
    font-weight: 400;
    font-size: 1.4rem;
  }
  header .menu ul li a::before {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 4px;
    background: linear-gradient(90deg, hsl(136deg, 65%, 51%) 0%, hsl(192deg, 70%, 51%) 100%);
    margin-bottom: -3.1rem;
    z-index: 1;
    transform: scaleX(0);
    transform-origin: center;
    transition: transform 0.3s ease-in-out;
  }
  header .menu ul li a:hover {
    cursor: pointer;
  }
  header .menu ul li a:hover::before {
    transform: scaleX(1);
    transform-origin: center;
  }
  header .access-btn {
    display: inline-block;
  }
  header .access-btn a {
    font-size: 1.4rem;
    background-image: linear-gradient(90deg, hsl(136deg, 65%, 51%) 0%, hsl(192deg, 70%, 51%) 100%);
    padding: 1.4rem 3.9rem;
    border-radius: 3rem;
    display: inline-block;
    color: hsl(0deg, 0%, 100%);
  }
  header .access-btn a:hover {
    background-image: linear-gradient(90deg, hsl(136deg, 65%, 70%) 0%, hsl(192deg, 70%, 70%) 100%);
  }
}
@media (min-width: 992px) {
  header {
    padding: 0 12.5rem;
  }
}
@media (min-width: 1200px) {
  header {
    padding: 0 16.5rem;
  }
}

.overlay {
  display: none;
  background: linear-gradient(0deg, rgba(255, 255, 255, 0.6671043417) 0%, rgba(17, 17, 17, 0.4290091036) 51%, rgba(0, 0, 0, 0.4290091036) 100%);
  position: absolute;
  width: 100%;
  height: 100vh;
  left: 0;
  bottom: 0;
  top: 0;
  z-index: 3;
}
.overlay.menu-open {
  display: block;
}

.ham {
  width: 3rem;
  margin: 0;
  z-index: 10;
}
.ham span {
  display: block;
  background-color: black;
  height: 0.1rem;
  width: 2.4rem;
  margin: 0.5rem auto;
  border-radius: 2px;
}
.ham:hover {
  cursor: pointer;
}
.ham .br-1 {
  transition: transform 0.4s ease-in-out;
}
.ham .br-1.animado {
  transform: rotate(-45deg) translate(-4.5px, 4.5px);
  transition: transform 0.4s ease-in-out;
}
.ham .br-2 {
  transition: opacity 0.3s ease-in-out;
}
.ham .br-2.animado {
  opacity: 0;
}
.ham .br-3 {
  transition: transform 0.4s ease-in-out;
}
.ham .br-3.animado {
  transform: rotate(45deg) translate(-4px, -4px);
  transition: transform 0.4s ease-in-out;
}
@media (min-width: 768px) {
  .ham {
    display: none;
  }
}

main {
  overflow: hidden;
}
main .section-hero {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  text-align: center;
  padding-bottom: 4.4rem;
}
main .section-hero .img-main-mobile {
  width: 100%;
}
@media (min-width: 768px) {
  main .section-hero .img-main-mobile {
    display: none;
  }
}
main .section-hero .img-main-desktop {
  display: none;
}
@media (min-width: 768px) {
  main .section-hero .img-main-desktop {
    display: block;
  }
}
@media (min-width: 768px) {
  main .section-hero {
    flex-direction: row;
    text-align: start;
    padding-bottom: 0;
  }
  main .section-hero .info-main {
    flex: 0 1 47%;
